import React, { useState, useRef  } from 'react';
import { StyleSheet, View, Dimensions, Platform, Text } from 'react-native';
import { Video  } from 'expo-av';
import { 
  Menu,
  MenuOptions,
  MenuOption,
  MenuTrigger,
} from 'react-native-popup-menu';

const VideoPlayer = ({url, initSeek}) => {
    const [isPlaying, setPlaying] = useState(true)
    const [isLoading, setIsLoading] = useState(true);
    const [playSpeed, setPlaySpeed] = useState(0.5)
    // const [currentTime, setCurrentTime] = useState(initSeek)
    var currentTime = initSeek
    const video = useRef(null)
    // const videoSrc = parseInt(url)
    const videoSrc = url
    const window = Dimensions.get("window");

    // console.log("Video Player initSeek %d currentTime %d", initSeek, currentTime)
    // console.log("url %s", url)
    // console.log("Video src")
    // console.log(videoSrc)
    // console.log("Play Speed %f", playSpeed)
    // console.log("Device width %d height %d", window.width, window.height)
    // console.log(window)
    const videoHeight = Math.min(Math.floor(window.width / 1.7777), window.height)
    const videoWidth = Math.min(window.width, Math.floor(videoHeight * 1.7777))
    // console.log("Video height %d width %d Window height %d width %d", videoHeight, videoWidth,  window.height, window.width) 

    const onLoad = (data) => {
      // console.log("onLoad ")
      // console.log(data)
      // setDuration(data.durationMillis)
      // console.log("onLoad setting play status to true pause false")
      setPlaying(true)
      // setPaused(false)
      // console.log("Onload currentTime %d initSeek %d", currentTime, initSeek)
      video.current.playFromPositionAsync(initSeek)
    };

    const onUpdatePlaySpeed = (newSpeed) => {
      // console.log("Updating speed from %f to %f", playSpeed, newSpeed)
      // console.log("onUpdatePlaySpeed currentTime %d initSeek %d ", currentTime, initSeek)
      setPlaySpeed(newSpeed)
      video.current.playFromPositionAsync(currentTime)
    }
    const onStatusUpdate = (status) => {
      // console.log("onStatusUpdate ")
      // console.log(status)
      // setCurrentTime(status.positionMillis)
      currentTime = status.positionMillis
    }

    return (
        <View>
          <View>
            <Video
                onLoad={onLoad}
                ref={video}
                useNativeControls
                resizeMode={'cover'}
                source = {videoSrc}
                rate={playSpeed}
                // positionMillis  = {initSeek}
                shouldPlay = {isPlaying}
                style={[styles.backgroundVideo, {height: videoHeight, width:videoWidth} ]}
                isLooping
                onPlaybackStatusUpdate={onStatusUpdate}                 
            />
            </View>
            <View>
              <Menu>
                <MenuTrigger text='Video speed' />
                <MenuOptions>
                  <MenuOption onSelect={() => onUpdatePlaySpeed(0.1)} text='0.1' />
                  <MenuOption onSelect={() => onUpdatePlaySpeed(0.25)} text='0.25' />
                  <MenuOption onSelect={() => onUpdatePlaySpeed(0.5)} text='0.5' />
                  <MenuOption onSelect={() => onUpdatePlaySpeed(0.75)} text='0.75' />
                  <MenuOption onSelect={() => onUpdatePlaySpeed(1.0)} text='1.0' />
                  <MenuOption onSelect={() => onUpdatePlaySpeed(1.25)} text='1.25' />
                  <MenuOption onSelect={() => onUpdatePlaySpeed(1.5)} text='1.5' />
                </MenuOptions>
              </Menu>
            </View>
        </View>
    );
};

const styles = StyleSheet.create({
    backgroundVideo: {
    },
    menuContainer: {
      flex: 1,
      alignItems: 'left',
      justifyContent: 'center',
      paddingTop: 10,
      paddingBottom: 200,
      backgroundColor: '#ecf0f1',
    },    
});

export default VideoPlayer;