import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View } from 'react-native';

import { NavigationContainer } from '@react-navigation/native';
// import { createStackNavigator } from '@react-navigation/stack';
import { createNativeStackNavigator } from '@react-navigation/native-stack';

import { Menu, MenuProvider } from 'react-native-popup-menu';

import {TextScreen} from './screenSrc/TextScreen'
import {SplashScreen} from './screenSrc/SplashScreen';
import {VideoView} from './screenSrc/VideoView';
import {Assets} from './screenSrc/Assets'
import {HeatmapView} from './screenSrc/HeatmapView';

// export default function App() {
//   return (
//     <View style={styles.container}>
//       <Text style={styles.textStyle}>Open up App.js to start working on your app here!</Text>
//       <StatusBar style="auto" />
//     </View>
//   );
// }
const Stack = createNativeStackNavigator();

const App: () => Node = () => {
  // const isDarkMode = useColorScheme() === 'dark';

  // const backgroundStyle = {
  //   backgroundColor: isDarkMode ? Colors.darker : Colors.lighter,
  // };

  return (
    <MenuProvider>
    <NavigationContainer>
      <AppStack></AppStack>
    </NavigationContainer>
    </MenuProvider>
  );
};

const AppStack = () => {
  return(
  <Stack.Navigator>
    <Stack.Screen 
      name="Home"
      component = {SplashScreen}
      options={{ title: 'Welcome Michael!' }}
    />
    <Stack.Screen 
      name="TextScreen"
      component={TextScreen}
      options={{ title: 'Game summary' }}
    />    
    <Stack.Screen 
      name="VideoView"
      component={VideoView}
      options={{ title: 'View the Video' }}
    />
    <Stack.Screen 
      name="Assets"
      component={Assets}
      options={{ title: 'Display the assets' }}
    />
    <Stack.Screen 
      name="HeatmapScreen"
      component={HeatmapView}
      options={{ title: 'Position Heatmap' }}
    />       
  </Stack.Navigator>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
  textStyle: {
    color: '#0F0',
    fontSize: 18,
  },
});

export default App;