import 'react-native-gesture-handler';
import React, { useState, useRef } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
} from 'react-native';
import VideoPlayer from './videoPlayerExpo'

import {videoAsset} from '../index';

export const VideoView = ({ navigation, route }) => {
  const assetTopLeveIndex = route.params.assetIndex
  const idx = route.params.param1
// Convert a string mm:ss to seconds
  const splitView = (view) => {
    var segs = view.split(":");
    setInitSeek();
  };

  const url = videoAsset[assetTopLeveIndex][idx]
  
  //prop initSeek is a string ofset in format hh:mm:ss
  var segs = route.params.param2.split(":");
  // const initSeek = parseFloat(segs[0], 10)*3600 + parseFloat(segs[1], 10)*60 + parseFloat(segs[2],10)
  const initSeek = (parseFloat(segs[0], 10)*3600 + parseFloat(segs[1], 10)*60 + parseFloat(segs[2],10)) * 1000  // mSec
  // console.log("VideoView url %s",  url)
  // console.log("initSeek %s", initSeek)


  return (
    <View>
      <VideoPlayer url={url} initSeek={initSeek}/>
    </View>
  )
};

const StrokeViewStyles = StyleSheet.create({
  topContainer: {
    marginTop: 8,
    backgroundColor: "aliceblue",
    justifyContent: "center",
    marginLeft: "2%",
  },
  topRowEntry: {
    fontSize: 20,
    textAlign: "left",
    marginTop: "1%",
  },  
});
