import {React, useState} from 'react';
import { StyleSheet, Image, ScrollView, View, Text, Dimensions, Platform, } from 'react-native';
import {AddHeaderRow, TextScreenStyle} from './TextScreen';
import {heatMapAssets} from '../index';

export const HeatmapView = ({ navigation, route }) => {
  const json = route.params.json
  const jsonGame = route.params.jsonGame
  const textInfo = route.params.jsonTextInfo
  const assetTopLeveIndex = route.params.assetIndex
  const idx = parseInt(route.params.param1)

  const headText = textInfo["head_text"]
  const imageSrc = heatMapAssets[assetTopLeveIndex][idx]
  const {width: winWidth, height: winHeight} = Dimensions.get("window");
  
  var  [imgWidth, setImageWidth] = useState(0)
  var  [imgHeight, setImageHeight] = useState(0)
  // Platform dependencies required since setImageDims crashes on device
  // and resolveAssetSource is missing on web
  if (Platform.OS == "web") {
    const setImageDims = function  (w, h)  {
      setImageHeight(h)
      setImageWidth(w)
    }
    Image.getSize(imageSrc, setImageDims)
  } else {
    const imageDims = Image.resolveAssetSource(imageSrc)
    imgWidth = imageDims.width
    imgHeight = imageDims.height
  }

  // console.log("HeatMapView ImageSrc " + imageSrc)

  return (
      <View>
      <View key='headContainer' style={[TextScreenStyle.headerContainer, {flexDirection: "row", flexWrap: "wrap", }]}>
        {Object.keys(headText).map( idx => (
        <AddHeaderRow key={"AddRowheader"  + idx} json={json} jsonGame={jsonGame} 
        navigation={navigation} entry={headText[idx]} idx={idx}/>
        ))
      }
      </View>
      <ScrollView  contentContainerStyle={{alignItems: 'center'}} style={HeatmapStyle.scrollView}>
        <Image key="heatMapImage" source={imageSrc} resizeMode='contain'
        style={{
          width: imgWidth,
          height: imgHeight,
          maxHeight: winHeight,
          maxWidth: winWidth,
          marginTop: 0,
          }} />
      </ScrollView>      
    </View>
  );
};

const HeatmapStyle = StyleSheet.create({
  scrollView: {
    backgroundColor: "aliceblue",
    marginLeft: "2%",
    width: "100%",
  }
});