import 'react-native-gesture-handler';
import React, { useState } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
  ScrollView,
  Dimensions
} from 'react-native';

function AddTablerow(props){
  const json = props.json
  const navigation = props.navigation
  
  const tableEntry = props.entry
  const rowIdx = props.rowIdx
  const rowKey = props.rowKey
  const entryWidth = props.tableEntryWidth
  // const jsonTopLevel = props.jsonTopLevel

  if (tableEntry["destination"]) {
    let params = null
    let style = null
    let jsonTextInfo = tableEntry["jsonTextInfo"]
    let jsonMatch = props.jsonMatch
    if (jsonMatch == null) {
      // Json section representing the top level (game level)
      jsonMatch = json[tableEntry["jsonMatch"]]
    }

    if (tableEntry["destination"] == "TextScreen") {
      params = {"json": json, "jsonMatch": jsonMatch, "jsonTextInfo": jsonMatch[jsonTextInfo]}
    } else {
      // console.log("TableRow: Destination %s assetIndex %s param1 %s", tableEntry["destination"], tableEntry["jsonMatch"], tableEntry["param1"])
      params = {"json": json, "jsonMatch": jsonMatch, "jsonTextInfo": jsonMatch[jsonTextInfo], "assetIndex": tableEntry["jsonMatch"], "param1": tableEntry["param1"], "param2":tableEntry["param2"]}
    }
    if (tableEntry["style"] == "Link") {
      style = TextScreenStyle.tableRowEntryClickLinkStyle
    } else {
      style = TextScreenStyle.tableRowEntryClickRowStyle
    }
    return  <TouchableOpacity style={[TextScreenStyle.tableRow,  {width: entryWidth}]} key={"touch"+rowKey}
          onPress={() =>
            navigation.navigate(tableEntry["destination"], params)
          }>

        <Text key={rowKey} style={[style,]}>
            {tableEntry["value"]} 
        </Text>
      </TouchableOpacity>    
  } else {
    let rowStyle = null
    if (rowIdx == 0) {
      rowStyle = TextScreenStyle.tableRowEntryLeft
    } else {
      rowStyle = TextScreenStyle.tableRowEntryOther
    }
    return <View style={[rowStyle, {width: entryWidth} ]}>
        <Text key={rowKey} style={rowStyle}>
          {tableEntry["value"]}
        </Text>
      </View>
  }
}

export function AddHeaderRow(props){
  const json = props.json
  const jsonMatch = props.jsonMatch
  const navigation = props.navigation

  const entry = props.entry
  const idx = props.idx

  if (entry["destination"]) {
    if (jsonMatch == null) {
      // Json section representing the top level (game level)
      jsonMatch = json[tableEntry["jsonMatch"]]
    }

    let params = null
    const jsonTextInfo = entry["jsonTextInfo"]
    if (entry["destination"] == "TextScreen") {
      params = {"json": json, "jsonMatch": jsonMatch, "jsonTextInfo": jsonMatch[jsonTextInfo]}
    } else {
      // console.log("HeaderRow: Destination %s assetIndex %s param1 %s", entry["destination"], entry["jsonMatch"], entry["param1"])
      params = {"json": json, "jsonMatch": jsonMatch, "jsonTextInfo": jsonMatch[jsonTextInfo], "assetIndex": entry["jsonMatch"], "param1": entry["param1"], "param2":entry["param2"]}
    }
    let key = ""
    if (entry["key"]) {
      key=entry["key"]+": "
    }

    return <TouchableOpacity key={"headerRowTouch" + idx}
          onPress={() =>
            navigation.navigate(entry["destination"], params)
          }>
          <Text key={'headerRow' + idx} style={TextScreenStyle.headerClickableLink}>
            {key}{entry["value"]} 
        </Text>
    </TouchableOpacity>       
  } else {
    return <Text key={'headerRow' + idx} style={TextScreenStyle.headerRowEntry}>
      {entry["key"]}: {entry["value"]} 
    </Text>
  }
}
export const TextScreen = ({ navigation, route }) => {
  const json = route.params.json
  const jsonMatch = route.params.jsonMatch
  const textInfo = route.params.jsonTextInfo

  const headText = textInfo["head_text"]
  const tableHeader = textInfo["table_header"]
  const tableContent = textInfo["table_content"]
  const tableEntryWidth = Math.floor(100 / tableHeader.length).toString() + "%"
  const {width, height} = Dimensions.get("window");

  return (
    <View  style={ {height: "90%", maxHeight: height}}>
      <View key='headContainer' style={[TextScreenStyle.headerContainer, {flexDirection: "row", flexWrap: "wrap", }]}>
        {Object.keys(headText).map( idx => (
        <AddHeaderRow key={"AddheaderRow" + idx} json={json} jsonMatch={jsonMatch}
        navigation={navigation} entry={headText[idx]} idx={idx}/>
        ))
      }
      </View>

      {/* Table First the header row */}
      <View key='tableHeader' style={[TextScreenStyle.tableContainer, {flexDirection: "column", flexWrap: "wrap", }, {marginTop: 40,}]}>
        <View style={[TextScreenStyle.tableHeader,]}>
          {Object.keys(tableHeader).map((idx) => (
            <Text key={'tableHeader'+ idx} style={[TextScreenStyle.headerEntry, {width: tableEntryWidth}]} >
              {tableHeader[idx]["value"]}
            </Text>
          ))}
        </View>
      </View>

      {/* Table entries */}

      <View key='tableContent' style={[TextScreenStyle.tableContainer ]}>
        <ScrollView  key='tableContentScroll' style={TextScreenStyle.tableScrollContainer} contentContainerStyle={{flexGrow: 1 }} >
          {Object.keys(tableContent).map(idx => (
              <View key={"tableRow" + idx} style={[TextScreenStyle.tableContainer, {flexDirection: "row", flexWrap: "wrap", }]}>
              {Object.keys(tableContent[idx]).map(rowIdx => (
                <AddTablerow key={"AddRow"  + idx + rowIdx} json={json} jsonMatch={jsonMatch} 
                  navigation={navigation} entry={tableContent[idx][rowIdx]} tableEntryWidth={tableEntryWidth}
                  rowKey={"tableRowEntry" + idx + rowIdx} rowIdx={rowIdx}/>
              ))}
            </View>
            ))}
        </ScrollView>
      </View>
    </View>
  )
};


