import 'react-native-gesture-handler';
import React, { useEffect, useState } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
  ActivityIndicator,
} from 'react-native';

export const  SplashScreen = ({ navigation }) => {
  // var  json = require('../assets/gamedetail.json');
  var  json = require('../assets/deploy.json');
  const [isLoading, setLoading] = useState(false);
  setTimeout(() => {navigation.navigate("TextScreen", {"json": json, 'jsonMatch': null, "jsonTextInfo": json["home_screen"]});}, 2500);

  return (
    <View key='topV' style={[SplashStyles.container, {flexDirection: "column", flexWrap: "wrap", }]}>
      {isLoading ? <ActivityIndicator/> : (
        <TouchableOpacity  style={SplashStyles.mainEntry}
        onPress={() =>
          navigation.navigate("TextScreen", {"json": json, 'jsonMatch': null, "jsonTextInfo": json["home_screen"]})
        }>
      <View style={[SplashStyles.mainEntry]}>
        <Text key="SplashTextMain" style={[SplashStyles.mainEntry]}>
            AI-SportAnalytics
      </Text>
      </View>
      <View style={[SplashStyles.bottomEntry]}>
      <Text key="SplashTextBottom" style={[SplashStyles.bottomEntry]}>
            Transforming sport performance
      </Text>
      </View>
      </TouchableOpacity>
      )}
      </View>
  );
};

const SplashStyles = StyleSheet.create({
  container: {
    justifyContent: 'center',
    backgroundColor: 'lightskyblue',
    height: '100%'
  },
  mainEntry: {
    color: "black",
    width: "100%",
    textAlign: "center",
    textAlignVertical: "center",
    fontSize: 30,
    fontFamily: "monospace",
  },
  bottomEntry: {
    marginTop: 35,
    color: "green",
    width: "100%",
    textAlign: "center",
    textAlignVertical: "bottom",
    fontSize: 20,
    fontFamily: "monospace",
  },  
});
